.contactUs{
    position: relative;
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 50px;

    &:after{
        content: '';
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        background: url("https://source.unsplash.com/random") no-repeat center;
        background-size: cover;
        filter: blur(50px);
        z-index: -1;
    }

    .contact-box{
        max-width: 900px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        justify-content: center;
        align-items: center;
        text-align: center;
        background-color: #fff;
        box-shadow: 0px 0px 19px 5px rgba(0,0,0,0.19);
        padding: 20px 20px 20px 20px;
        border-radius: 5px;
        margin-bottom: 100px;

        .left{
            background: url("https://source.unsplash.com/random") no-repeat center;
            background-size: cover;
            height: 100%;
            border-radius: 5px;
        }
        
        .right{
            padding: 25px 40px;

            h2{
                position: relative;
                padding: 0 0 10px;
                margin-bottom: 30px;
                margin-left: 1rem;
            }
            
            // h2:after{
            //     content: '';
            //     position: absolute;
            //     left: 50%;
            //     bottom: 0;
            //     transform: translateX(-50%);
            //     height: 4px;
            //     width: 50px;
            //     border-radius: 2px;
            //     background-color: #000000;
            // }

            .field{
                width: 100%;
                border: 2px solid rgba(0, 0, 0, 0);
                outline: none;
                background-color: rgba(230, 230, 230, 0.6);
                padding: 0.5rem 1rem;
                font-size: 1rem;
                margin-bottom: 22px;
                transition: .3s;
                border-radius: 5px;
            }
            
            .field:hover{
                background-color: rgba(0, 0, 0, 0.1);
            }

            textarea{
                min-height: 150px;
            }
            
            .btn{
                width: 100%;
                padding: 0.5rem 1rem;
                margin: 1rem;
                background-color: #000000;
                color: white;
                font-size: 1.1rem;
                border: none;
                border-radius: 5px;
                outline: none;
                cursor: pointer;
                align-items: center;
                justify-content: flex-end;

                &:hover{
                    transition: 0.5s;
                    background-color: rgb(110, 110, 110);
                }
            }

            .field:focus{
                // border: 2px solid rgba(30,85,250,0.47);
                border: 2px solid gray;
                background-color: #fff;
            }
        }
    }


    @media screen and (max-width: 880px){
        .contact-box{
            grid-template-columns: 1fr;
        }
        .left{
            height: 200px;
        }
    }
}